let configUrl = "http://47.101.209.126:19005";
	
	
const request = ({url, data, method = "GET", contentType = 1}) => {
	// 
	let header = {
		"content-type": contentType === 1 ? "application/json" : "application/x-www-form-urlencoded"
	}
	// // token,token
	// if (uni.getStorageSync('token')) {
	// 	//! token
	// 	header.authorization = uni.getStorageSync('token').token.access_token;
		
	// }
	
	
	//  200 resolvereject
	return new Promise((resolve, reject) => {
		uni.request({
			url: configUrl + url,
			data,
			method,
			header,
			success: (res) => {
				const status = res.status
				let msg = ''


				if (status < 200 || status >= 300) {
					// http
					msg = showStatus(status)
					if (typeof res.data === 'string') {
						res.data = {msg}
					} else {
						res.data.message = msg
					}
				}
				
				
				//  if(res.data.code && res.data.code == '401') {
				//         // store.commit("tokenInfo", '')
				//         // localStorage.setItem('token', '');
				//         // store.commit('UserInfo', {});
				//         // store.commit('ifRouterWatch', false);
				//         // store.commit('PermissionsList', []);
				//         // store.commit('InfoRouterLists', []);
				//         // store.commit('InfoRoles', []);
				//         ElNotification({
				//             title: '',
				//             message: "",
				//             type: 'error'
				//         });
				//         window.location.href = "/Login";
				//     }
				//     return res
				// }, (error) => {
				//     // 
				//     error.data = {}
				//     error.data.message = ''
				//     return Promise.resolve(error)
				// })
				
				

			}
			
		})
	})
}

const showStatus = (status) => {
	let message = ''
	switch (status) {
		case 400:
			message = '(400)'
			break
		case 401:
			message = '(401)'
			break
		case 403:
			message = '(403)'
			break
		case 404:
			message = '(404)'
			break
		case 408:
			message = '(408)'
			break
		case 500:
			message = '(500)'
			break
		case 501:
			message = '(501)'
			break
		case 502:
			message = '(502)'
			break
		case 503:
			message = '(503)'
			break
		case 504:
			message = '(504)'
			break
		case 505:
			message = 'HTTP(505)'
			break
		default:
			message = `(${status})!`
	}
	return `${message}`
}

export default request;